body {
  background-color: #d0e7ff; 
  margin: 0;
  font-family: Arial, sans-serif;
}
.form-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin-top: 1in;
}

.form-content {
  width: 90%;
  max-width: 400px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.form-field {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
}

label {
  margin-right: 10px; 
   text-align: left; 
  margin-right: 10px;
}

input {
  width: 100%;
  max-width: 200px;

  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  background-color: #fff9c4;
}
.add-field-button {
  position: sticky;
  bottom: 0;
  background-color: white; /* Optional: Background to ensure visibility */
  margin-top: 10px;
  /*float: right; /* Aligns the button to the right */
  padding: 8px 12px;
  /*background-color: ##0b7dd;*/
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.add-field-button:hover {
  background-color:#0b7dda ;
}

.submit-button {
  margin-top: 20px;
  padding: 8px 12px;
  background-color: #45a049;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.submit-button:hover {
  background-color:#45a049 ;
}